// Import libraries
import React from "react";

// Import components
import { Layout } from "antd";
import Logo from "./Logo/index";
import MenuContainer from "./MenuContainer/index";

const MainSidebar = () => (
  <Layout.Sider>
    <Logo />
    <MenuContainer />
  </Layout.Sider>
)

export default MainSidebar;



